import { Component, OnInit, ViewChild } from '@angular/core';
import { Brand } from 'src/app/models/brand.model';
import { ITableContext, TableViewComponent } from 'src/app/shared/dashboard-resource/table-view/table-view.component';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent {
  data: Brand[];
  context: ITableContext<Brand> = {
    urls: {
      createUrl: 'create',
      detailsUrl: ':id'
    },
    data: this.data,
    properties: [
      {
        key: 'name',
        title: 'Name',
        type: 'string'
      },
      {
        key: 'description',
        title: 'Description',
        type: 'string'
      },
      {
        key: 'created_at',
        title: 'Created At',
        type: 'datetime'
      },
      {
        key: 'updated_at',
        title: 'Updated At',
        type: 'datetime'
      },
    ],
    title: 'Brands'
  };
  @ViewChild('table') table: TableViewComponent<Brand>;

  constructor(route: ActivatedRoute) {
    // console.log(this.context);
    route.data.subscribe(data => {
      this.data = data.brands;
      this.context.data = this.data;
    });
  }
}
