import { Component, OnInit, AfterViewInit, ViewChild, ElementRef } from '@angular/core';
import { Brand } from 'src/app/models/brand.model';
import { NgbModalRef, NgbModal, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';
import { Router } from '@angular/router';

@Component({
  selector: 'app-brand-details',
  templateUrl: './brand-details.component.html',
  styleUrls: ['./brand-details.component.scss']
})
export class BrandDetailsComponent implements OnInit, AfterViewInit {
  message: string;
  brand: Brand;
  attributes: string[];

  @ViewChild('content') content: ElementRef<HTMLElement>;
  private modal: NgbModalRef;

  constructor(private modalService: NgbModal, private router: Router) {
  }

  ngOnInit(): void {
    this.brand = history.state.resource;

    if (!this.brand)
    {
      this.ok();
      return;
    }
    this.attributes = Object.keys(this.brand);
    // tslint:disable-next-line: no-string-literal
    this.message = 'Details of brand: ' + this.brand['name'];
  }

  ngAfterViewInit(): void {
    const options: NgbModalOptions = {
      size: 'md',
      backdrop: 'static',
    };
    this.modal = this.modalService.open(this.content, options);
  }

  ok(): void {
    this.router.navigateByUrl('/brands');
    if (this.modal)
    {
      this.modal.dismiss();
    }
  }
}
