import { Component, OnInit } from '@angular/core';
import { Brand } from 'src/app/models/brand.model';
import { ResourceDeleteContext } from 'src/app/shared/dashboard-resource/resource-delete/resource-delete.component';

@Component({
  selector: 'app-brand-delete',
  templateUrl: './brand-delete.component.html',
  styleUrls: ['./brand-delete.component.scss']
})
export class BrandDeleteComponent implements OnInit {
  context: ResourceDeleteContext<Brand>;

  constructor() { }

  ngOnInit(): void {
    this.context = {
      deleteUrl: 'brandsDeleteUrl',
      data: history.state.resource,
      title: 'Delete Brand Resource!',
      description: 'Are you sure to delete this brand?'
    };
  }

}
