import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';

import { environment } from '../../../environments/environment';
import { Brand } from '../../models/brand.model';
import { Observable } from 'rxjs';

@Injectable()
export class BrandsResolver implements Resolve<Brand[]> {
    constructor(private http: HttpClient) {

    }

    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<Brand[]> {
        return this.http.get<Brand[]>(`${ environment.apiUrl }/${ environment.brandsFetchUrl }`);
    }
}
